<template>
  <div class="curriculum">
    <div class="search-form">
      <Form ref="formValidate" :model="searchData" inline :label-width="40">
        <FormItem label="日期" prop="startTime" >
          <DatePicker :clearable="false" v-model="searchData.startTime" type="date" placeholder="开始时间" style="width: 200px"></DatePicker>
        </FormItem>
        <FormItem label="—" class="date-line" prop="endTime" :label-width="20">
          <DatePicker :clearable="false" v-model="searchData.endTime" type="date" placeholder="结束时间" style="width: 200px"></DatePicker>
        </FormItem>
        <FormItem label="姓名" prop="name">
          <!-- eslint-disable -->
          <Select style="width: 160px" v-model="searchData.name" :label-width="60" placeholder="学生姓名">
            <Option v-for="(label,value) in studentsMap" :key="value" :value="value">{{label}}</Option>
          </Select>
        </FormItem>
        <FormItem :label-width="20">
            <Button type="primary" @click="search()">查询</Button>
            <Button @click="reset()" style="margin-left: 8px">重置</Button>
        </FormItem>
      </Form>
    </div>
    <div class="add-part">
      <Button type="primary" @click="exportTable()">导出</Button>
    </div>
     <div class="table-part">
       <Table height="700" ref="table" stripe border :columns="columns" :data="data"></Table>
     </div>
    <Modal :width="700" v-model="modal" title="选择学生" @on-ok="modalOk" @on-cancel="modalCancel">
      <div class="modal-content">
        <!-- eslint-disable -->
        <Input style="width:200px" :maxlength="4" v-model="searchStudent" placeholder="姓名"></Input>
        <Row :gutter="10">
          <Col span="4" style="margin-top:10px" v-for="(name,id) in dealStudentsMap" :key="id">
            <Button style="width:100%" :type="selectCol.studentId===id?'primary':'default'" @click="chooseStudent(id)">{{name}}</Button>
            <!-- eslint-disable -->
          </Col>
        </Row>
      </div>
      <div slot="footer">
        <Button @click="modalCancel()" style="margin-right: 10px">取消</Button>
        <Button type="primary" @click="modalOk()">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {

  data () {
    return {
      scheduleMap: {
        mor0800: '08:00',
        mor0830: '08:30',
        mor0900: '09:00',
        mor0930: '09:30',
        mor1000: '10:00',
        mor1030: '10:30',
        mor1100: '11:00',
        mor1130: '11:30',
        mor1200: '12:00',
        mor1230: '12:30',
        aft1300: '13:00',
        aft1330: '13:30',
        aft1400: '14:00',
        aft1430: '14:30',
        aft1500: '15:00',
        aft1530: '15:30',
        aft1600: '16:00',
        aft1630: '16:30',
        aft1700: '17:00',
        aft1730: '17:30',
        aft1800: '18:00',
        aft1830: '18:30',
        aft1900: '19:00',
        aft1930: '19:30',
        aft2000: '20:00',
        aft2030: '20:30',
        aft2100: '21:00',
        aft2130: '21:30',
        aft2200: '22:00',
        aft2230: '22:30',
        aft2330: '23:00'
      },
      dateMap: {},
      searchData: {
        startTime: '',
        endTime: '',
        name: ''
      },
      data: [],
      columns: [],
      defaultColumn: {
        title: '时间',
        align: 'center',
        key: 'time',
        fixed: 'left',
        width: 130,
        render: (h, params) => {
          return h('div', this.scheduleMap[params.row.time])
        }
      },
      studentsMap: {},
      modal: false,
      searchStudent: '',
      selectCol: {
        studentName: '',
        studentId: '',
        time: '',
        date: '',
        id: ''
      },
      edit: false,
      delect: false
    }
  },
  computed: {
    dealStudentsMap () {
      if (!this.searchStudent) {
        return this.studentsMap
      }
      let map = {}
      for (const key in this.studentsMap) {
        if (this.studentsMap[key].indexOf(this.searchStudent) >= 0) {
          map[key] = this.studentsMap[key]
        }
      }
      return map
    }
  },
  created () {
    this.initDate()
    this.initTable()
    this.$indexedDB.initStore('students', this.queryStudents)
    this.$indexedDB.initStore('curriculum', this.query)
  },
  methods: {
    search () {
      this.initTable()
      this.query()
    },
    exportTable () {
      let { startTime, endTime } = this.setTime()
      this.$refs.table.exportCsv({
        filename: `${startTime}-${endTime}课时统计`,
        data: this.data.map(item => {
          for (const key in item) {
            if (key === 'time') {
              item[key] = this.scheduleMap[item[key]]
            } else if (item[key]) {
              item[key] = this.studentsMap[item[key]]
            }
          }
          return item
        })
      })
      this.data = []
      this.search()
    },
    addName (row, d) {
      this.modal = true
      this.selectCol.time = row.time
      this.selectCol.date = d
      this.selectCol.id = row.id
      if (row[d]) {
        this.edit = true
        this.selectCol.studentId = row[d]
        this.selectCol.studentName = this.studentsMap[row[d]]
      } else {
        this.edit = false
      }
    },
    chooseStudent (id) {
      if (this.selectCol.studentId === id) {
        this.selectCol.studentId = ''
        this.selectCol.studentName = ''
        this.delect = true
      } else {
        this.selectCol.studentId = id
        this.selectCol.studentName = this.studentsMap[id]
        this.delect = false
      }
    },
    modalOk () {
      if (!this.selectCol.studentId && !this.delect) {
        this.$Message.info('请选择学生')
        return
      }
      if (this.edit) {
        this.editPost()
      } else {
        this.addPost()
      }
    },
    modalCancel () {
      this.modal = false
      this.selectCol.studentId = ''
      this.selectCol.studentName = ''
      this.selectCol.time = ''
      this.selectCol.date = ''
      this.selectCol.id = ''
      this.searchStudent = ''
    },
    editPost () {
      this.$indexedDB.http({
        method: 'put',
        url: 'curriculum',
        data: this.selectCol
      }).then((res) => {
        this.$Message.success(res.message)
        this.modalCancel()
        this.query()
      })
    },
    addPost () {
      let data = { ...this.selectCol }
      delete data.id
      this.$indexedDB.http({
        method: 'add',
        url: 'curriculum',
        data: data
      }).then((res) => {
        this.$Message.success(res.message)
        this.modalCancel()
        this.query()
      })
    },
    initDate () {
      this.searchData.startTime = this.$lib.showWeekFirstDay()
      this.searchData.endTime = this.$lib.showWeekLastDay()
      this.searchData.name = ''
    },
    initTable () {
      this.setDefalutData()
      this.setColums()
    },
    query () {
      this.$indexedDB.http({
        method: 'get',
        url: 'curriculum'
      }).then((res) => {
        for (const r of res.data) {
          for (const d of this.data) {
            if (r.time === d.time) {
              d[r.date] = r.studentId
              d.id = r.id
            }
          }
        }
      })
    },
    queryStudents () {
      this.$indexedDB.http({
        method: 'get',
        url: 'students'
      }).then((res) => {
        for (const student of res.data) {
          if (!student.isDelect) {
            this.$set(this.studentsMap, student.id, student.name)
          }
        }
      })
    },
    setColums () {
      let { startTime, endTime } = this.setTime()
      let dates = this.$lib.getAll(startTime, endTime)
      let name = this.searchData.name
      let self = this
      this.columns = []
      this.columns.push(this.defaultColumn)
      for (const d of dates) {
        let week = this.$lib.myMoment(d).formate('ddd')
        let weekNum = new Date(d).getDay()
        this.columns.push({ title: `${d}(${week})`,
          key: d,
          align: 'center',
          minWidth: 150,
          render (h, params) {
            return h('div', {
              attrs: {
                class: `name-part week-${weekNum} ${(name && params.row[d] && params.row[d] === name) ? 'choosed' : ''}`
              },
              on: {
                click: () => {
                  self.addName(params.row, d)
                }
              }
            }, self.studentsMap[params.row[d]])
          }
        })
      }
    },
    setDefalutData () {
      let { startTime, endTime } = this.setTime()
      let dates = this.$lib.getAll(startTime, endTime)
      this.data = []
      for (const d of dates) {
        this.dateMap[d] = ''
      }
      for (const key in this.scheduleMap) {
        this.data.push({
          time: key,
          ...this.dateMap
        })
      }
    },
    setTime () {
      let { startTime, endTime } = this.searchData
      startTime = this.$lib.myMoment(new Date(startTime)).formate('YYYY-MM-DD')
      endTime = this.$lib.myMoment(new Date(endTime)).formate('YYYY-MM-DD')
      return { startTime, endTime }
    },
    reset () {
      this.initDate()
      this.initTable()
      this.query()
    }
  }
}
</script>

<style lang='less' scoped>

</style>
